import { useState, useEffect } from "react";
import styles from "./AdminUser.module.css";

interface Users {
  id: string;
  name: string;
  createdAt: string;
  recentConnect: string;
  interestArea: Array<string>;
}
function AdminUser() {
  const [users, setUsers] = useState<Users[]>([]);
  useEffect(() => {
    setUsers([
      {
        id: "0",
        name: "",
        createdAt: "20230228",
        recentConnect: "202302281000",
        interestArea: ["", "", ""],
      },

      {
        id: "1",
        name: "",
        createdAt: "20230328",
        recentConnect: "202304261020",
        interestArea: ["", ""],
      },
      {
        id: "2",
        name: "",
        createdAt: "20230528",
        recentConnect: "202302281000",
        interestArea: ["", "", ""],
      },
      {
        id: "3",
        name: "",
        createdAt: "20230725",
        recentConnect: "202302281104",
        interestArea: ["", ""],
      },
    ]);

    console.log(users);
  }, []);

  const onClickDestroy = (id: string) => {
    const answer = prompt(
      `  ("${id}")   .`
    );
    if (answer === null) alert("  .");
    else if (answer === id)
      setUsers((current) => current.filter((user) => user.id !== id));
    else if (answer != id)
      alert("id      .");
  };
  const sortId = (n: number) => {
    const sortedId = [...users].sort((a, b) => {
      if (a.id < b.id) return n;
      if (a.id > b.id) return -n;
      return 0;
    });
    setUsers(sortedId);
  };
  const sortName = (n: number) => {
    const sortedName = [...users].sort((a, b) => {
      if (a.name < b.name) return n;
      if (a.name > b.name) return -n;
      return 0;
    });
    setUsers(sortedName);
  };
  const sortCreatedAt = (n: number) => {
    const sortedCreatedAt = [...users].sort((a, b) => {
      if (a.createdAt < b.createdAt) return n;
      if (a.createdAt > b.createdAt) return -n;
      return 0;
    });
    setUsers(sortedCreatedAt);
  };

  return (
    <div className={styles.container}>
      <table className={styles.table}>
        <tbody>
          <tr>
            <th>
              id
              <button onClick={() => sortId(-1)}></button>
              <button onClick={() => sortId(+1)}></button>
            </th>
            <th>
              
              <button onClick={() => sortName(-1)}></button>
              <button onClick={() => sortName(1)}></button>
            </th>
            <th>
               
              <button onClick={() => sortCreatedAt(-1)}></button>
              <button onClick={() => sortCreatedAt(1)}></button>
            </th>
            <th> </th>
            <th> </th>
            <th></th>
          </tr>
          {users.map((user) => (
            <tr key={user.id}>
              <td>{user.id}</td>
              <td>{user.name}</td>
              <td>{user.createdAt}</td>
              <td>{user.recentConnect}</td>
              <td>
                {user.interestArea.map((area, index) => (
                  <span key={`${user.id}${index}_inter`}>
                    {area}
                    {user.interestArea.length - 1 != index ? "," : null}
                  </span>
                ))}
              </td>
              <td>
                <button
                  style={{ background: "rgb(228, 84, 84)" }}
                  onClick={() => onClickDestroy(user.id)}
                >
                  
                </button>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}

export default AdminUser;
