import { useEffect } from "react";
import { useRef } from "react";

declare global {
  interface Window {
    kakao: any;
  }
}
const { kakao } = window;

function MapContainer({
  address,
  title,
}: {
  address?: string | null;
  title?: string | null;
}): JSX.Element {
  const ref = useRef<HTMLDivElement>(null);
  useEffect(() => {
    let container = ref.current; //   DOM 
    let options = {
      //     
      center: new window.kakao.maps.LatLng(33.450701, 126.570667), // .
      level: 3, // (,  )
    };
    let map = new kakao.maps.Map(container, options); //    

    var geocoder = new kakao.maps.services.Geocoder();

    //   
    geocoder.addressSearch(address, function (result: any, status: any): any {
      //   
      if (status === kakao.maps.services.Status.OK) {
        var coords = new kakao.maps.LatLng(result[0].y, result[0].x);

        //     
        var marker = new kakao.maps.Marker({
          map: map,
          position: coords,
        });

        //     
        var infowindow = new kakao.maps.InfoWindow({
          content: `<a href="https://map.kakao.com/link/map/${address},${result[0].y},${result[0].x}" target="_blank"><div style="width:150px;text-align:center;padding:6px 0;">${title}</div></a>`,
        });
        infowindow.open(map, marker);

        //      
        map.setCenter(coords);
      }
    });
    let locationAccess = 0;
    let message;

    if (locationAccess == 0) {
      if (navigator.geolocation) {
        // GeoLocation    
        navigator.geolocation.getCurrentPosition(function (position: any): any {
          var lat = position.coords.latitude, // 
            lon = position.coords.longitude; // 

          var locPosition = new kakao.maps.LatLng(lat, lon); //    geolocation   
          message = '<div style="padding:5px;"> </div>'; //   
          var marker = new kakao.maps.Marker({
            map: map,
            position: new kakao.maps.LatLng(lat, lon),
          });
          //   
          var infowindow = new kakao.maps.InfoWindow({
            content: `<div style="width:150px;text-align:center;padding:6px 0;"> </div>`,
          });
          infowindow.open(map, marker);
          if (locationAccess == 1) {
            map.setCenter(locPosition); //    
          }
          locationAccess = 1;
        });
      }
    }
  }, []);

  return <div id="map" ref={ref} style={{ width: "100vw", height: "300px" }} />;
}

export default MapContainer;
