import { useState } from "react";
import DaumPostcode from "react-daum-postcode";
import { useNavigate } from "react-router-dom";
import styles from "./ReviewFac.module.css";

function ReviewFac({ setReviewData }: any) {
  const [openPostcode, setOpenPostcode] = useState(false);
  const [sido, setSido] = useState<string>("");
  const [sigungu, setSigungu] = useState<string>("");
  const [newAddress, setNewAddress] = useState("");
  const [oldAddress, setOldAddress] = useState<string>("");
  const [buildingName, setBuildingName] = useState("");
  const [residenceType, setResidenceType] = useState("");
  const [residenceFloor, setResidenceFloor] = useState("");
  const [pros, setPros] = useState("");
  const [cons, setCons] = useState("");
  const [livedYear, setLivedYear] = useState(0);
  const [star, setStar] = useState(0);
  const [addressTitle, setAddressTitle] = useState("  ");
  const [lat, setLat] = useState(0); // 35.xx
  const [lng, setLng] = useState(0); // 127.xx
  const navigate = useNavigate();
  let now = new Date();
  let nowYear = now.getFullYear();
  let years = new Array(nowYear - 2020);
  for (let i = 2020; i <= nowYear; i++) {
    years[i - 2020] = i;
  }
  years.reverse();
  /**
   * handler
   */
  const handle = {
    //   
    clickButton: () => {
      setOpenPostcode((current) => !current);
    },

    //   
    selectAddress: (data: any) => {
      setBuildingName(data.buildingName);
      setNewAddress(data.roadAddress);
      setOldAddress(data.jibunAddress);
      setSido(data.sido);
      setAddressTitle("  ");
      setSigungu(data.sigungu);
      if (data.buildingName === "") {
        const newBuilding = data.roadAddress
          .replace(data.sido, "")
          .replace(data.sigungu, "")
          .trim();
        setBuildingName(newBuilding);
      }
      if (data.sido === "") {
        setSido("");
        setSigungu("");
      }
      if (data.sido === "") setSido("");
      if (data.sido)
        console.log(`
              : ${data.roadAddress},
              : ${data.zonecode},
               : ${data.jibunAddress},
               : ${data.sido},
              : ${data.sigungu},
              
          `);
      let geocoder = new kakao.maps.services.Geocoder();
      geocoder.addressSearch(
        data.roadAddress,
        function (result: any, status: any): any {
          //   
          if (status == kakao.maps.services.Status.OK) {
            setLat(result[0].y);
            setLng(result[0].x);
            console.log(lat, lng);
          }
        }
      );
      setOpenPostcode(false);
    },
  };

  const onSubmit = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
  };
  const onChange = (e: React.ChangeEvent<HTMLTextAreaElement>) => {
    const {
      target: { name, value },
    } = e;
    if (name === "pros") setPros(value);
    if (name === "cons") setCons(value);
    console.log(pros, cons);
  };

  const sendReview = () => {
    if (buildingName === "") alert(" .");
    else if (residenceType === "") alert(" ");
    else if (residenceFloor === "") alert(" ");
    else if (livedYear === 0) alert(" .");
    else if (pros === "") alert(" .");
    else if (cons === "") alert(" .");
    else if (star === 0) alert(" .");
    else {
      setReviewData((prev: any) => [
        ...prev,
        {
          reviewId: Math.floor(Math.random() * 10000) + 1,
          building: buildingName,
          newAddress: newAddress,
          oldAddress: oldAddress,
          pros: pros,
          cons: cons,
          residenceType: residenceType,
          residenceFloor: residenceFloor,
          livedYear: livedYear,
          star,
          lat,
          lng,
          sido,
          sigungu,
        },
      ]);

      navigate("/review");
      alert(" !");
    }
  };

  return (
    <div className={styles.container}>
      <form className={styles.form} onSubmit={onSubmit}>
        <label htmlFor="address"></label>
        <div
          id="address"
          className={`${styles.addressInput} ${
            newAddress === "" || styles.active
          }`}
          onClick={() => setOpenPostcode(true)}
          title={addressTitle}
        >
          {newAddress === "" ? (
            <>  </>
          ) : (
            <div className={styles.addressInformation}>
              <div>{buildingName}</div>
              <div>{newAddress}</div>
              <div>{oldAddress}</div>
            </div>
          )}
        </div>
        <label></label>
        <div className={styles.buttons}>
          <div
            className={`${styles.mediumBtn} ${
              residenceType === "" && styles.active
            }`}
            onClick={() => setResidenceType("")}
          >
            
          </div>
          <div
            className={`${styles.mediumBtn} ${
              residenceType === "" && styles.active
            }`}
            onClick={() => setResidenceType("")}
          >
            
          </div>
          <div
            className={`${styles.mediumBtn} ${
              residenceType === "//" && styles.active
            }`}
            onClick={() => setResidenceType("//")}
          >
            //
          </div>
        </div>
        <label></label>
        <div className={styles.buttons}>
          <div
            className={`${styles.mediumBtn} ${
              residenceFloor === "" && styles.active
            }`}
            onClick={() => setResidenceFloor("")}
          >
            
          </div>
          <div
            className={`${styles.mediumBtn} ${
              residenceFloor === "" && styles.active
            }`}
            onClick={() => setResidenceFloor("")}
          >
            
          </div>
          <div
            className={`${styles.mediumBtn} ${
              residenceFloor === "" && styles.active
            }`}
            onClick={() => setResidenceFloor("")}
          >
            
          </div>
        </div>
        <label></label>
        <div className={styles.buttons}>
          {years.map((year) => (
            <div
              key={year}
              className={`${styles.mediumBtn} ${
                livedYear === year && styles.active
              }`}
              onClick={() => setLivedYear(year)}
            >
              {year}
            </div>
          ))}
        </div>
        <label></label>
        <textarea
          value={pros}
          name="pros"
          onChange={onChange}
          placeholder="  100"
          rows={1}
          maxLength={100}
        />
        <label></label>
        <textarea
          value={cons}
          name="cons"
          onChange={onChange}
          placeholder="  100"
          rows={1}
          maxLength={100}
        />
        <label> </label>
        <div className={styles.stars}>
          <div
            className={`${styles.star} ${star >= 1 ? styles.active : null}`}
            onClick={() => setStar(1)}
          >
            
          </div>
          <div
            className={`${styles.star} ${star >= 2 ? styles.active : null}`}
            onClick={() => setStar(2)}
          >
            
          </div>
          <div
            className={`${styles.star} ${star >= 3 ? styles.active : null}`}
            onClick={() => setStar(3)}
          >
            
          </div>
          <div
            className={`${styles.star} ${star >= 4 ? styles.active : null}`}
            onClick={() => setStar(4)}
          >
            
          </div>
          <div
            className={`${styles.star} ${star >= 5 ? styles.active : null}`}
            onClick={() => setStar(5)}
          >
            
          </div>
        </div>
        <div className={styles.submitBtns}>
          <div className={styles.mediumBtn} onClick={() => navigate("/review")}>
            
          </div>
          <div className={styles.mediumBtn} onClick={sendReview}>
            
          </div>
        </div>
      </form>

      <div>
        {openPostcode && (
          <>
            <div
              className={styles.topContainer}
              onClick={() => setOpenPostcode(false)}
            >
                X
            </div>
            <DaumPostcode
              className={styles.daumAddressForm}
              onComplete={handle.selectAddress} //     
              autoClose={false} //     DOM    
              defaultQuery="" //     
            />
          </>
        )}
      </div>
    </div>
  );
}

export default ReviewFac;
